ul.game {
    height: 5rem;
    a {
        .game {
            height: 5rem;
        }
        img.image.fit.invert {
            filter: invert(1);
        }
    }
}
a.game {
    img {
        align-self: center;
        width: 100%;
        max-height: 6vh;
        & .invert {
            filter: invert(1);
        }
    }
    img.invert {
        filter: invert(1);
    }
}
div.game {
    display: flex !important;
    align-content: center;
    justify-content: center;
    span {
        display: flex;
        align-items: center;
    }
}
ul.actions {
    justify-content: center;
}
.video-player {
    position: relative;

    padding-bottom: 56.25%; /* 16:9 */
}

.video-player__frame {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;
}
