/* Image */

	.image {
		border: 0;
		display: inline-block;
		position: relative;

		img,iframe {
			display: block;
		}

		&.left,
		&.right {
			max-width: 40%;

			img,iframe {
				width: 100%;
			}
		}

		&.left {
			float: left;
			margin: 0 1.5rem 1rem 0;
			top: 0.25rem;
		}

		&.right {
			float: right;
			margin: 0 0 1rem 1.5rem;
			top: 0.25rem;
		}

		&.fit {
			display: block;
			margin: 0 0 _size(element-margin) 0;
			width: 100%;

			img,iframe {
				width: 100%;
			}
		}

		&.main {
			display: block;
			margin: 0;
			width: 100%;

			img,iframe {
				width: 150vw;
				max-width: 70%;
				filter: drop-shadow(16px 16px 20px black);
				margin-left: auto;
				margin-right: auto;
			}
		}

		@include breakpoint('<=small') {
			&.main {
				margin: 0 0 _size(element-margin) 0;
			}
		}
	}