// Misc.
$misc: (
  z-index-base: 10000,
  gallery-lightbox-opacity: 0.875,
);

// Duration.
$duration: (
  menu: 0.5s,
  nav: 0.5s,
  transition: 0.2s,
  bg-fadein: 2s,
  bg-transition: 3s,
  bg-slide: 45s,
  // (lower = faster, higher = slower)
  gallery-lightbox-fadein: 0.5s,
);

// Size.
$size: (
  border-radius: 10px,
  border-width: 1px,
  element-height: 3rem,
  element-margin: 2rem,
  letter-spacing: 0.25rem,
  letter-spacing-alt: 0.1rem,
);

// Font.
$font: (
  family: (
    "04B03",
    monospace,
    sans-serif,
  ),
  family-head: (
    "04B03",
    monospace,
    sans-serif,
  ),
  family-fixed: (
    "04B03",
    monospace,
  ),
  weight: 400,
  weight-light: 400,
  weight-semi-bold: 400,
  weight-bold: 900,
  weight-head: 600,
);

// Palette.
$palette: (
  bg: #000000,
  bg-alt: #282828,
  fg: #ffffff,
  fg-bold: #f9f9f9,
  fg-light: rgba(#ffffff, 0.75),
  border: rgba(255, 255, 255, 0.25),
  border-bg: rgba(255, 255, 255, 0.065),
  border2: rgba(0, 0, 0, 0.25),
  border2-bg: rgba(0, 0, 0, 0.065),
  invert: (
    bg: #ffffff,
    bg-alt: #f9f9f9,
    fg: #282828,
    fg-bold: #000000,
    fg-light: rgba(#282828, 0.5),
    border: rgba(#637485, 0.25),
    border-bg: rgba(#637485, 0.075),
    border2: rgba(#637485, 0.4),
    border2-bg: rgba(#637485, 0.075),
  ),
);
